/* ------------------------------------------------------------------------------------------------ */
/* Form */
/* ------------------------------------------------------------------------------------------------ */

.form-group {
  &.pswd {
    position: relative;
    .ic-showpswrd {
      position: absolute;
      top: 35px;
      right: 14px;
      height: 13px;
      opacity: .5;
      cursor: pointer;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain; } }
    &-show {
      .ic-showpswrd {
        opacity: 1; } } } }

.col-md-6.form-group.pswd {
  .ic-showpswrd {
    right: 28px; } }

.form-control {
  height: 40px;
  border: 1px solid $border;
  border-radius: 4px;
  font-size: 14px;
  line-height: 20px;
  padding: 10px 12px;
  outline: none !important;
  &::placeholder {
    color: #A8A8A8; } }

select.select-control {
  height: 40px;
  border: 1px solid $border;
  border-radius: 4px;
  font-size: 14px;
  line-height: 20px;
  padding: 10px 12px;
  outline: none !important; }

.bootstrap-select {
  &> .dropdown-toggle {
    background: #FFF;
    border: 1px solid $border;
    border-radius: 4px;
    padding: 7px 18px; }
  .dropdown-item {
    color: #767676;
    padding: 8px 10px;
    border-radius: 8px;
    &:hover {
      color: #767676;
      background: #F6F6F6; }
    &:active,
    &.active {
      color: $text;
      background: #FFF; }
    &.active:hover {
      background: #F6F6F6; } }
  .dropdown-menu {
    padding: 10px 8px;
    margin: 10px 0;
    border: 1px solid $border; } }
.btn-light:not(:disabled):not(.disabled).active, .btn-light:not(:disabled):not(.disabled):active, .show > .btn-light.dropdown-toggle {
  background: #FFF;
  border: 1px solid $border;
  box-shadow: none; }
.bootstrap-select .dropdown-toggle:focus, .bootstrap-select > select.mobile-device:focus + .dropdown-toggle {
  outline: none !important;
  box-shadow: none; }
