/* ------------------------------------------------------------------------------------------------ */
/* General */
/* ------------------------------------------------------------------------------------------------ */

.page-transition {
  position: fixed;
  left: 0%;
  top: 0%;
  right: 0%;
  bottom: 0%;
  z-index: 99999999;
  display: block;
  width: 100vw;
  height: 100vw;
  background-color: #FFF; }

main {
  overflow: hidden; }

.container {
  @include maxtablet() {
    padding-left: 32px;
    padding-right: 32px; } }

.btn {
  font-size: 16px;
  line-height: 24px;
  padding: 7px 24px;
  border-radius: 8px;
  font-weight: 400;
  transition: all .3s ease;
  @include maxtablet() {
    font-size: 12px;
    line-height: 16px;
    padding: 6px 16px; }
  &:hover {
    text-decoration: none; }
  &:focus, &.focus {
    outline: 0;
    box-shadow: none; }
  &.disabled,
  &:disabled {
    opacity: 0.5; }
  .icon {
    display: inline-block;
    width: 18px;
    height: 18px;
    &:first-child {
      margin-right: 8px; }
    &:last-child {
      margin-left: 8px; } }
  span {
    display: inline-block; }
  // BTN PRIMARY
  &-primary {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-primary:hover {
    color: #FFF;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%); }
  &-primary:focus, &-primary.focus {
    color: #fff;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%);
    box-shadow: none; }
  &-primary.disabled, &-primary:disabled {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-primary:not(:disabled):not(.disabled):active,
  &-primary:not(:disabled):not(.disabled).active,
  .show > &-primary.dropdown-toggle {
    color: #fff;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%); }
  &-primary:not(:disabled):not(.disabled).active:focus,
  &-primary:not(:disabled):not(.disabled):active:focus,
  .show > &-primary.dropdown-toggle:focus {
    box-shadow: none; }
  // BTN KLIEN
  &-klien {
    color: #FFF;
    background-color: $klien;
    border-color: $klien; }
  &-klien:hover {
    color: #FFF;
    background-color: darken($klien, 10%);
    border-color: darken($klien, 10%); }
  &-klien:focus, &-klien.focus {
    color: #fff;
    background-color: darken($klien, 10%);
    border-color: darken($klien, 10%);
    box-shadow: none; }
  &-klien.disabled, &-klien:disabled {
    color: #FFF;
    background-color: $klien;
    border-color: $klien; }
  &-klien:not(:disabled):not(.disabled):active,
  &-klien:not(:disabled):not(.disabled).active,
  .show > &-klien.dropdown-toggle {
    color: #fff;
    background-color: darken($klien, 10%);
    border-color: darken($klien, 10%); }
  &-klien:not(:disabled):not(.disabled).active:focus,
  &-klien:not(:disabled):not(.disabled):active:focus,
  .show > &-klien.dropdown-toggle:focus {
    box-shadow: none; }
  // BTN TALENTA
  &-talenta {
    color: #FFF;
    background-color: $talenta;
    border-color: $talenta; }
  &-talenta:hover {
    color: #FFF;
    background-color: darken($talenta, 10%);
    border-color: darken($talenta, 10%); }
  &-talenta:focus, &-talenta.focus {
    color: #fff;
    background-color: darken($talenta, 10%);
    border-color: darken($talenta, 10%);
    box-shadow: none; }
  &-talenta.disabled, &-talenta:disabled {
    color: #FFF;
    background-color: $talenta;
    border-color: $talenta; }
  &-talenta:not(:disabled):not(.disabled):active,
  &-talenta:not(:disabled):not(.disabled).active,
  .show > &-talenta.dropdown-toggle {
    color: #fff;
    background-color: darken($talenta, 10%);
    border-color: darken($talenta, 10%); }
  &-talenta:not(:disabled):not(.disabled).active:focus,
  &-talenta:not(:disabled):not(.disabled):active:focus,
  .show > &-talenta.dropdown-toggle:focus {
    box-shadow: none; }
  // BTN BLACK
  &-black {
    color: #FFF;
    background-color: #000;
    border-color: #000; }
  &-black:hover {
    color: #FFF;
    background-color: darken(#000, 10%);
    border-color: darken(#000, 10%); }
  &-black:focus, &-black.focus {
    color: #fff;
    background-color: darken(#000, 10%);
    border-color: darken(#000, 10%);
    box-shadow: none; }
  &-black.disabled, &-black:disabled {
    color: #FFF;
    background-color: #000;
    border-color: #000; }
  &-black:not(:disabled):not(.disabled):active,
  &-black:not(:disabled):not(.disabled).active,
  .show > &-black.dropdown-toggle {
    color: #fff;
    background-color: darken(#000, 10%);
    border-color: darken(#000, 10%); }
  &-black:not(:disabled):not(.disabled).active:focus,
  &-black:not(:disabled):not(.disabled):active:focus,
  .show > &-black.dropdown-toggle:focus {
