  // BTN WHITE
  &-white {
    color: $primary;
    background-color: #FFF;
    border-color: #FFF;
    &.klien {
      color: $klien; }
    &.talenta {
      color: $talenta; } }
  &-white:hover {
    color: $primary;
    background-color: darken(#FFF, 10%);
    border-color: darken(#FFF, 10%);
    &.klien {
      color: $klien; }
    &.talenta {
      color: $talenta; } }
  &-white:focus, &-white.focus {
    color: $primary;
    background-color: darken(#FFF, 10%);
    border-color: darken(#FFF, 10%);
    box-shadow: none;
    &.klien {
      color: $klien; }
    &.talenta {
      color: $talenta; } }
  &-white.disabled, &-white:disabled {
    color: $primary;
    background-color: #FFF;
    border-color: #FFF;
    &.klien {
      color: $klien; }
    &.talenta {
      color: $talenta; } }
  &-white:not(:disabled):not(.disabled):active,
  &-white:not(:disabled):not(.disabled).active,
  .show > &-white.dropdown-toggle {
    color: $primary;
    background-color: darken(#FFF, 10%);
    border-color: darken(#FFF, 10%);
    &.klien {
      color: $klien; }
    &.talenta {
