    border-color: $klien; }
  &-border-klien:not(:disabled):not(.disabled).active:focus,
  &-border-klien:not(:disabled):not(.disabled):active:focus,
  .show > &-border-klien.dropdown-toggle:focus {
    box-shadow: none; }
  // BTN BORDER TALENTA
  &-border-talenta {
    color: $talenta;
    background-color: transparent;
    border-color: $talenta; }
  &-border-talenta:hover {
    color: #fff;
    background-color: $talenta;
    border-color: $talenta; }
  &-border-talenta:focus, &-border-talenta.focus {
    color: #fff;
    background-color: $talenta;
    border-color: $talenta;
    box-shadow: none; }
  &-border-talenta.disabled, &-border-talenta:disabled {
    color: $talenta;
    background-color: transparent;
    border-color: $talenta; }
  &-border-talenta:not(:disabled):not(.disabled):active,
  &-border-talenta:not(:disabled):not(.disabled).active,
  .show > &-border-talenta.dropdown-toggle {
    color: #fff;
    background-color: $talenta;
    border-color: $talenta; }
  &-border-talenta:not(:disabled):not(.disabled).active:focus,
  &-border-talenta:not(:disabled):not(.disabled):active:focus,
  .show > &-border-talenta.dropdown-toggle:focus {
