.btn-link-black {
  color: $text;
  text-decoration: underline;
  transition: all .3s ease;
  &:hover {
    color: $secondary; } }

.btn-link-arrow {
  position: relative;
  color: $text;
  text-decoration: underline;
  transition: all .3s ease;
  &:after {
    content: "";
    display: inline-block;
    vertical-align: middle;
    margin-left: 2px;
    width: 1.2em;
    height: 1.2em;
    background: url(../images/ic-chevron-right.svg);
    background-repeat: no-repeat;
    background-position: center;
    // opacity: 0
    transform: translateX(0);
    transition: all .4s ease-in-out; }
  &:hover {
    color: $text;
    opacity: .5;
    &:after {
      opacity: 1;
      transform: translateX(4px); } } }

.link-primary {
  color: $primary;
  transition: all .3s ease;
  &:hover {
    color: $primary;
    text-decoration: underline; } }

.accordion {
  > .card {
    position: relative;
    border: 0;
    border-radius: 30px !important;
    margin-bottom: 30px;
    overflow: hidden;
    background: #F6F6F6;
    &:before {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 80%;
      left: 0;
      border-radius: 30px;
      background: #F6F6F6;
      opacity: 0;
      transition: all .3s ease-in-out; }
    &:last-child {
      margin-bottom: 0; }
    &.open {
      &:before {
        top: 0;
        opacity: 1;
        transition-delay: .1s; }
      .card-header .chevron {
        top: calc(50% - 0px); } }
    > .card-header {
      position: relative;
      z-index: 2;
      color: #BFBFBF;
      cursor: pointer;
      border-radius: 30px;
      border: 0;
      background: transparent;
      padding: 32px 90px 32px 32px;
      font-size: 18px;
      line-height: 1.5em;
      font-weight: 500;
      transition: all .3s ease .2s;
      margin: 0;
      .chevron {
        position: absolute;
        right: 32px;
        display: block;
        width: 16px;
        top: calc(50% - 8px);
        transform: none;
        transition: all .3s ease;
        .svg {
          display: block;
          width: 16px;
          height: 16px;
          path {
            fill: #BFBFBF !important; } } }
      &:not(.collapsed) {
        border-radius: 30px 30px 0 0;
        color: $text;
        .chevron {
          transform: rotateX(180deg);
          .svg path {
            fill: #FFFFFF !important; } }
        &:before {
          opacity: 1; } } }
    .card-body {
      position: relative;
      z-index: 2;
      color: #565656;
      background: transparent;
      border-radius: 0 0 30px 30px;
      padding: 10px 32px 32px;
      transition: all .3s ease;
      margin: 0; }
    .card-img {
      display: none; } }
  &--klien {
    > .card {
      &:before {
        background: $klien; }
      > .card-header:not(.collapsed) {
        color: #FFF; }
      .card-body {
        color: #FFF; } } }
  @include maxtablet {
    > .card {
      border-radius: 15px !important;
      &:before {
        border-radius: 15px; }
      .card-body {
        position: relative;
        z-index: 2;
        background: $primary;
        border-radius: 0 0 15px 15px; }
      .card-img {
        display: block;
        position: relative;
        width: 100%;
        padding-top: 90%;
        background: #E5E5E5;
        margin-top: -30px;
        img {
          display: block;
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          object-fit: cover;
          object-position: left top; } } }
    &--klien {
      > .card {
        .card-body {
          background: $klien; } } } } }


.badge {
  display: inline-block;
  font-size: 14px;
  line-height: 30px;
  border: 2px solid #FFF;
  border-radius: 16px;
  padding: 0 20px;
  color: #FFF;
  font-weight: 500;
  opacity: 1;
  transition: all .3s ease-in-out;
  .icon {
    top: -1px;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 16px;
    width: auto;
    margin-right: 8px;
    path {
      fill: #FFF; } }
  &:hover {
    opacity: 0.6;
    color: #FFF; }
  &--black {
    border: 2px solid #000;
    color: #000;
    .icon path {
      fill: #000; }
    &:hover {
      opacity: 0.6;
      color: #000; } } }


.pagination {
  justify-content: center;
  .page-link {
    display: block;
    border: 0;
    margin: 0;
    color: $text;
    font-size: 14px;
    line-height: 20px;
    font-weight: 600;
    padding: 8px;
    min-width: 38px;
    text-align: center;
    border-radius: 8px;
    background: transparent;
    transition: all .3s ease;
    img {
      display: block; }
    &:hover {
      background: $primary; }
    &:focus {
      box-shadow: none; } }
  .page-item {
    padding: 0 2px;
    &.active {
      .page-link {
        background: $primary;
        color: #FFF;
        pointer-events: none; } }
    &:first-child .page-link {
      border-radius: 8px;
      margin-right: 10px; }
    &:last-child .page-link {
      border-radius: 8px;
      margin-left: 10px; } } }
