/* ------------------------------------------------------------------------------------------------ */
/* Animation */
/* ------------------------------------------------------------------------------------------------ */


.bounce-1 {
  animation-name: bounce-1;
  animation-timing-function: linear; }
@keyframes bounce-1 {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(-10px); }
  100% {
    ransform: translateY(0); } }

@keyframes slider_dot {
  0% {
    width: 100%;
    left: 0; }
  80% {
    width: calc(200% + 6px);
    left: 0; }
  90% {
    width: 100%;
    left: calc(100% + 6px); }
  100% {
    width: 100%;
    left: calc(100% + 6px); } }


