/* ------------------------------------------------------------------------------------------------ */
/* Header */
/* ------------------------------------------------------------------------------------------------ */

.header {
  position: fixed;
  z-index: 200;
  display: block;
  width: 100%;
  left: 0;
  top: 0;
  padding: 20px 0;
  background: rgba(255,255,255,0.95);
  // background: #FFF
  border-radius: 0 0 30px 30px;
  transition: all .3s ease-in-out;
  .sm-only {
    display: none; }
  &__logo {
    img {
      display: block;
      width: auto;
      height: 24px;
      margin-top: 8px;
      margin-bottom: 8px; } }
  &__menu {
    .main-menu {
      display: flex;
      width: 100%;
      justify-content: flex-end;
      align-items: center;
      &__item {
        display: block;
        position: relative;
        padding: 0 16px;
        &:first-child {
          padding-left: 0; }
        &:last-child {
          padding-right: 0; }
        &.has-sub {
          .main-menu__link {
            position: relative;
            padding-right: 1.25em;
            .svg {
              display: block;
              position: absolute;
              width: 0.75em;
              height: 0.75em;
              top: calc(50% - 0.3em);
              right: 0;
              transform: rotateX(0);
              transition: all .3s ease;
              path {
                transition: all .3s ease; } } }
          @include minltablet() {
            &:hover {
              .main-menu__link {
                color: $primary;
                .svg {
                  transform: rotateX(180deg);
                  path {
                    fill: $primary !important; } } }
              .sub-menu {
                transform: rotateX(0);
                opacity: 1;
                visibility: visible; } } } }
        &.active {
          .main-menu__link {
            color: $primary; } } }
      &__link {
        display: block;
        color: #000000;
        font-size: 16px;
        line-height: 40px;
        transition: all .3s ease;
        &:hover {
          color: $primary;
          text-decoration: none; } }
      a.btn {
        min-width: 120px; } }
    .sub-menu {
      display: block;
      position: absolute;
      top: 100%;
      left: 0;
      background: #FFF;
      box-shadow: 2px 2px 8px #00000014;
      padding: 15px 0;
      border: 1px solid #f1f1f1;
      border-radius: 10px;
      transform: rotateX(90deg);
      transform-origin: top;
      opacity: 0;
      visibility: hidden;
      transition: all .3s ease;
      &__item {
        &.menu-klien {
          a:hover {
            color: $klien; } }
        &.menu-talenta {
          a:hover {
            color: $talenta; } } }
      &__link {
        display: block;
        padding: 5px 15px;
        white-space: nowrap;
        color: $text;
        transition: all .3s ease;
        &:hover {
          color: $primary;
          text-decoration: none; } } } }
  &.sticky {
    padding: 8px 0;
    box-shadow: 2px 2px 8px #00000014; }

  &--klien {
    .main-menu {
      &__item {
        &.has-sub {
          &:hover {
            .main-menu__link {
              color: $klien;
              .svg path {
                fill: $klien !important; } } }
          &.active {
            .main-menu__link {
              color: $klien; }
            .menu-klien {
              a {
                color: $klien; } } } } }
      &__link:hover {
        color: $klien; } } }
  &--talenta {
    .main-menu {
      &__item {
        &.has-sub {
          &:hover {
            .main-menu__link {
              color: $talenta;
              .svg path {
                fill: $talenta !important; } } }
          &.active {
            .main-menu__link {
              color: $talenta; }
            .menu-talenta {
              a {
                color: $talenta; } } } } }
      &__link:hover {
        color: $talenta; } } } }


@include maxtablet() {
  .header {
    border-radius: 0 0 15px 15px;
    padding-top: 15px;
    padding-bottom: 15px;
    .hide-sm {
      display: none; }
    .sm-only {
      display: block; }
    &__logo {
      display: block;
      width: 100px;
      img {
        height: 15px; } }
    &__menu {
      position: fixed;
      z-index: 2;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: #FFF;
      opacity: 0;
      visibility: hidden;
      transition: all .4s ease-in-out;
      .sub-menu {
        display: block;
        position: relative;
        box-shadow: none;
        border: 0;
        padding: 0;
        max-height: 0;
        &__link {
          font-size: 24px;
          line-height: 1.5em; } }
      .main-menu {
        display: block;
        text-align: center;
        padding: 80px 36px;
        &__item {
          padding: 13px 0;
          &.has-sub.sm-show {
            .main-menu__link {
              color: $primary;
              .svg {
                transform: rotateX(180deg);
                path {
                  fill: $primary !important; } } }
            .sub-menu {
              transform: rotateX(0);
              opacity: 1;
              visibility: visible;
              max-height: max-content; } } }
        &__link {
          display: inline-block;
          font-size: 26px;
          line-height: 1.5em;
          font-weight: 500; }
        .menu-klien > a {
          color: $klien; }
        .menu-talenta > a {
          color: $talenta; } }
      .menu-action {
        text-align: center;
        padding: 0 36px;
        .btn {
          margin: 0 6px; } }
      .menu-bot {
        font-size: 12px;
        line-height: 1.5em;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        span.copy {
          display: block;
          color: #fff;
          padding-top: 12px; }
        .menu-action {
          padding: 32px 0; }
        .menu-social {
          margin: 0 -6px 16px;
          .item {
            display: inline-block;
            padding-left: 6px;
            padding-right: 6px;
            .svg,
            img {
              width: 20px;
              height: 20px; } } }
        .menu-bot-ft {
          border-radius: 15px 15px 0 0;
          background: $primary;
          padding: 24px 0;
          a {
            color: #fff;
            text-decoration: underline;
            &:hover {
              color: #FFF; } } } } }
    .menu-icon {
      position: absolute;
      z-index: 3;
      top: calc(50% - 12px);
      right: 15px;
      width: 24px;
      height: 24px;
      span {
        display: block;
        width: 100%;
        height: 2px;
        border-radius: 2px;
        background: #000000;
        position: absolute;
        left: 0;
        trnasform: none;
        transition: all .3s ease-in-out;
        &:nth-child(1) {
          top: calc( 50% - 4px ); }
        &:nth-child(2) {
          top: calc( 50% + 4px ); } } } }

  body.menu-open {
    .header {
      &__menu {
        opacity: 1;
        visibility: visible; }
      .menu-icon {
        span {
          &:nth-child(1) {
            top: calc( 50% - 2px );
            transform: rotate(135deg); }
          &:nth-child(2) {
            top: calc( 50% - 2px );
            transform: rotate(-135deg); } } } } } }

