.masthead {
  padding: 80px 0;
  &__sub {
    display: block;
    margin-bottom: 0; }
  .masthead__title {
    margin-bottom: 32px;
    &:last-child {
      margin-bottom: 0; } }
  &__action {
    .btn {
      font-weight: 500;
      min-width: 170px; } }
  &__desc {
    span.hightlight {
      color: $primary; } }
  &__img {
    margin-bottom: 48px;
    &:last-child {
      margin-bottom: 0; } }
  &--home {
    .masthead__content {
      .masthead__sub {
        .item {
          display: none;
          &.active {
            display: block; } } }
      .masthead__title {
        position: relative;
        overflow: hidden;
        .item-title {
          display: none;
          span.desc {
            padding-top: 6px;
            display: block;
            font-size: 16px;
            line-height: 28px;
            font-weight: 400; }
          &.active {
            display: block; } } }
      .masthead__action {
        .item {
          display: none;
          &.active {
            display: block; } } }
      &.active {
        .masthead__sub {
          .alt-1 {
            display: none; }
          .alt-2 {
            display: block; } }
        .masthead__title {
          .alt-1 {
            transform: translateY(-100%); }
          .alt-2 {
            transform: translateY(0); } }
        .masthead__action {
          .alt-1 {
            display: none; }
          .alt-2 {
            display: block; } } } }
    .slider__item {
      position: relative;
      transition: all .3s ease;
      &.animate-left {
        transform: translateX(15px); }
      &.animate-right {
        transform: translateX(-15px); } }
    .owl-carousel {
      button.owl-dot.active {
        span {
          position: relative;
          background: $border;
          &:before {
            content: "";
            display: block;
            position: absolute;
            z-index: 2;
            width: 100%;
            height: 100%;
            background: #000;
            top: 0;
            left: 0;
            border-radius: 3px;
            animation: slider_dot 4.5s infinite; } }
        &:last-child span::before {
          width: 100% !important;
          left: 0 !important; } } } }

  &--about {
    position: relative;
    &:before {
      content: "";
      display: block;
      width: 100%;
      height: 50%;
      position: absolute;
      bottom: 0;
      left: 0;
      background: #F6F6F6;
      border-radius: 30px 30px 0 0; }
    .masthead {
      &__title {
        color: $primary;
        max-width: 904px;
        margin: 0 auto;
        margin-bottom: 32px !important; }
      &__img {
        margin-bottom: 40px; } } }
  &--klien {
    .masthead__sub {
      color: $klien; }
    span.hightlight {
      color: $klien; } }
  &--talenta {
    .masthead__title {
      font-size: 41px; }
    .masthead__sub {
      color: $talenta; }
    span.hightlight {
      color: $talenta; } }
  &--category {
    padding-bottom: 120px; }
  &--blog {
    .masthead__title {
      font-size: 22px;
      line-height: 1.5em;
      margin-bottom: 12px; }
    .masthead__cat-form {
      margin-bottom: 32px;
      label {
        display: block;
        margin-right: 10px;
        white-space: nowrap; }
      .form-inline {
        flex-wrap: nowrap; } }
    .masthead__desc {
      p {
        font-size: 12px;
        line-height: 18px; } }
    .masthead__action {
      padding-top: 64px;
      .btn-link-arrow {
        font-size: 12px;
        line-height: 18px; } }
    .masthead__box {
      border-radius: 30px;
      overflow: hidden;
      margin: 0;
      align-items: stretch;
      &-reverse {
        flex-direction: row-reverse;
        .masthead__img .badge-wraper {
          left: auto;
          right: 36px; } } }
    .masthead__img {
      position: relative;
      padding: 0;
      margin: 0;
      .badge-wraper {
        position: absolute;
        z-index: 10;
        top: 36px;
        left: 36px;
        margin-left: -8px;
        margin-right: -8px;
        .badge {
          margin-left: 8px;
          margin-right: 8px; } }
      .img {
        display: block;
        position: relative;
        width: 100%;
        padding-top: 58%; }
      img {
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
        object-position: 50% 50%; } }
    .masthead__content {
      display: flex;
      justify-content: center;
      flex-direction: column;
      padding: 32px;
      background: #F0F0F0; } }

  @include maxtablet() {
    padding: 48px 0;
    overflow: hidden;
    text-align: center;
    .slider {
      max-width: 90%;
      margin: 0 auto; }
    .owl-carousel .owl-nav button.owl-prev {
      left: -40px; }
    .owl-carousel .owl-nav button.owl-next {
      right: -40px; }
    .masthead__sub {
      font-size: 14px;
      line-height: 1.5em; }
    .masthead__title {
      font-size: 26px;
      line-height: 1.333em;
      margin-bottom: 24px; }
    .masthead__action {
      .btn {
        min-width: 98px; } }
    .masthead__content {
      margin-bottom: 48px; }
    .masthead__desc {
      p {
        font-size: 14px;
        line-height: 1.5em; } }
    &--about {
      .masthead__title:last-child {
        margin-bottom: 24px; }
      .masthead__img {
        max-width: 80%;
        margin: 0 auto 48px; } }
    &--category {
      text-align: left; } } }

